import { gsap } from "gsap";
import Draggable from "gsap/Draggable";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(Draggable);

Draggable.create('.card-wrap', {
  type: 'scroll'
});

gsap.set(".ball", {xPercent: -50, yPercent: -50});

const ball = document.querySelector(".ball");
const pos = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
const mouse = { x: pos.x, y: pos.y };
const speed = 0.35;

const xSet = gsap.quickSetter(ball, "x", "px");
const ySet = gsap.quickSetter(ball, "y", "px");

window.addEventListener("mousemove", e => {   
  mouse.x = e.x + 50;
  mouse.y = e.y - 50; 
})

window.addEventListener("mousedown", e => {   
  mouse.x = e.x + 50;
  mouse.y = e.y - 50; 
})



gsap.ticker.add(() => {
  
  // adjust speed for higher refresh monitors
  const dt = 1.0 - Math.pow(1.0 - speed, gsap.ticker.deltaRatio()); 
  
  pos.x += (mouse.x - pos.x) * dt;
  pos.y += (mouse.y - pos.y) * dt;
  xSet(pos.x);
  ySet(pos.y);
});

const fancyText = document.querySelector('.fancy-text')

function updateGradientVars(e) {
  const yPercent = Math.ceil((e.y / window.innerHeight) * 100);
  const xPercent = Math.ceil((e.x / window.innerWidth) * 100);
  fancyText.setAttribute('style', `--pos1: ${100 - xPercent}% center; --pos2: ${(100 - xPercent) / 2}% ${(100 - yPercent)}%;`)

}
